@use '../../styles.scss';

.main-container {
  min-height: 50vh; 
  display: flex; 
  flex-direction: column; 
  font-size: styles.$textFontSize;
  margin-top: 5%; 
  max-width: styles.$maxSectionWidth;
  margin-left: 10%;
}
.flicker-text {
  font-size: styles.$titleFontSize;
  color: styles.$linkcolor;
}
.focus-text {
  color: styles.$textcolor;
}

.icon {
  width: 35px; 
  height: 35px; 
  margin-right: 10px; 
  margin-top: 10px; 
  filter: invert(92%) sepia(6%) saturate(3060%) hue-rotate(337deg) brightness(87%) contrast(81%); // https://codepen.io/sosuke/pen/Pjoqqp
}

/* Animation */
.text-focus-in {
	-webkit-animation: text-focus-in 2.5s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
	        animation: text-focus-in 2.5s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
}

.highlight_text {
  color: styles.$linkcolor;
}

.text-flicker-in-glow {
	-webkit-animation: text-flicker-in-glow 2s linear both;
	        animation: text-flicker-in-glow 2s linear both;
}


